header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #000000;
    padding: 10px 20px;
    color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
  }

  header {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    }
  
  .logo {
    display: flex;
    align-items: center;
  }
  
  .logo img {
    margin-right: 10px;
  }
  
  nav ul {
    display: flex;
  }
  
  nav ul li {
    margin-left: 20px;
  }
  
  nav ul li:first-child {
    margin-left: 0;
  }
  
  nav ul li a {
    color: #fff;
    text-decoration: none;
    transition: all 0.3s ease;
    font-size: 18px;
  }
  
  nav ul li a:hover {
    color: #f8f9fa;
  }
  
  
  .logo {
    display: flex;
    align-items: center;
    margin-right: 20px;
  }
  
  .logo img {
    margin-right: 10px;
  }
  
  nav ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  nav ul li {
    margin-left: 20px;
  }
  
  nav ul li:first-child {
    margin-left: 0;
  }
  
  nav ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    padding: 10px;
    transition: all 0.3s ease;
  }
  
  /* Profile Button Styles */
  
  .profile-button {
    position: relative;
    margin-left: auto;
  }
  
  .profile-button button {
    background-color: black;
    color: white;
    border: 2px solid;
    padding: 10px;
    font-size: 18px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .profile-button button:hover {
    background-color: white;
    color: black;
  }
  
  .user-info {
    position: absolute;
    top: 40px;
    right: 0;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    padding: 30px;
    z-index: 1;
    display: flex;
    align-items: center;
  }
  
  .user-info .user-photo {
    padding: 15px;
    margin-right: 10px;
    border-radius: 50%;
  }
  
  .user-info .user-details h3 {
    color: black;
    margin: 0 0 5px 0;
    font-size: 18px;
    font-weight: bold;
  }
  
  .user-info .user-details p {
    color: black;
    margin: 0 0 5px 0;
    font-size: 14px;
  }
  
  .logout-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 20px;
    font-size: 14px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .logout-button:hover {
    background-color: #0062cc;
  }
  
  .SearchButton {
    background-color: #000000;
    margin: 0 0 5px 0;
    border: none;
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    transition: all 0.3s ease;
    cursor: pointer;
  }

  /* Header Styles for Small Devices */

@media only screen and (max-width: 767px) {

    header {
      padding: 10px;
    }
  
    .logo {
      margin-right: 0;
    }
  
    .logo img {
      max-height: 50px;
    }
  
    nav ul {
      position: absolute;
      top: 60px;
      left: 0;
      right: 0;
      background-color: #000000;
    }
  
    nav ul li {
      width: 100%;
      text-align: center;
      margin: 0;
      padding: 10px 0;
    }
  
    nav ul li a {
      font-size: 16px;
    }
  
    .profile-button {
      margin-left: 0;
    }
  
    .profile-button button {
      padding: 5px;
      font-size: 16px;
    }
  
    .user-info {
      top: 50px;
      right: 10px;
      left: auto;
    }
  
    .user-info .user-details h3 {
      font-size: 16px;
    }
  
    .user-info .user-details p {
      font-size: 12px;
    }
  
    .logout-button {
      padding: 10px;
      font-size: 12px;
    }
  
  }
  

  @media only screen and (max-width: 360px) {
    header {
    padding: 5px;
    }
    
    .logo {
    font-size: 16px;
    margin-right: 10px;
    }
    
    .logo img {
    height: 30px;
    }
    
    nav ul li a {
    font-size: 14px;
    padding: 5px;
    }
    
    .profile-button button {
    font-size: 14px;
    padding: 5px;
    }
    
    .user-info {
    top: 30px;
    right: 5px;
    padding: 10px;
    }
    
    .user-info .user-photo {
    padding: 5px;
    margin-right: 5px;
    width: 30px;
    height: 30px;
    }
    
    .user-info .user-details h3 {
    font-size: 16px;
    }
    
    .user-info .user-details p {
    font-size: 12px;
    }
    
    .logout-button {
    padding: 10px;
    font-size: 12px;
    }
    }

    .event-search {
        background-color: black;
        position: fixed;
        left: 0px;
        top: 18%;
        width: 400px;
        height: 100vh;
        padding: 20px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
        transition: all 0.5s;
      }

      
      .event-search.show {
        left: 0;
      }
      
      .event-search h2 {
        font-size: 24px;
        margin-bottom: 20px;
      }
      
      .checkbox-container {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
      }
      
      .checkbox-item {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
      }
      
      .checkbox-item input {
        margin-right: 10px;
      }
      
      .search-container {
        margin-top: 20px;
      }
      
      .search-item {
        display: flex;
        margin-bottom: 10px;
      }
      
      .search-item label {
        flex: 0 0 120px;
        font-weight: bold;
      }
      
      .search-item input {
        flex: 1;
        padding: 5px;
        border: 1px solid #ccc;
      }
      
      .search-button-container {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
      }
      
      .search-button {
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        font-size: 16px;
        color: #fff;
        background-color: #007bff;
        cursor: pointer;
        transition: all 0.2s;
      }
      
      .search-button:hover {
        background-color: #0069d9;
      }
      
      .search-button:active {
        transform: scale(0.98);
      }
      